.product-list-container {
    margin: 50px auto;
}

.product-list {
    margin: auto;
    width: 92% !important;
    will-change: auto !important;
}
.product-list::-webkit-scrollbar{
    width: 0;
    height: 2px;
}
.product-list::-webkit-scrollbar-thumb{
    width: 1px;
    background: #007bff;
}
.product-list::-webkit-scrollbar-thumb:hover{
    filter: brightness(150%);
} 