#sell-body {
    animation: fade-in 0.5s forwards;
}

#sell-body #search-bar{
    margin:auto;
    width: 50vw;
    text-align: left;
}

#searchbar-heading{
    margin-top: 30vh;
    text-align: center;
}

#sample-listing > div.product-container{
    width: 100% !important;
    margin: 0px;
    border: 0px;
}

#result-container{
    position: fixed;
    left: 0;
    top: 0px;
    width: 100%;
    border: 1px solid #000;
    height: 100%;
    background-color: #555;
    background-color: rgba(0,0,0,0.3);
    z-index: 1;
}

*.sample-listing {
    border-radius: 5px 5px 0 0;
    width: 100%;
}
.sample-listing-input {
    border: 0;
    border-radius: 5px;
    font-size: 14px;
    padding: 2px 4px;
    outline: 1px solid #ccc;
    transition: outline 0.35s;
    width: 180px;
}

.sample-listing-input:focus {
    outline: 1px solid #007bff;
}

#result-container{
    position: fixed;
    left: 0;
    top: 0px;
    width: 100%;
    border: 1px solid #000;
    height: 100%;
    background-color: #555;
    background-color: rgba(0,0,0,0.3);
    z-index: 1;
}
.sample-listing-container{
    min-height: 450px;
    vertical-align: top;
    width: 350px;
    text-align: center;
    display: inline-block;
    border-radius: 5px;
    border: 0.5px solid lightgrey;
    animation-duration: 1s;
    animation-iteration-count: infinite;
    animation-direction: alternate;
    position: relative;
    text-align: center !important;
}
 .image-container{
    width: 100%;
}
 .product-image{
    width: 100%;
    border-radius: 5px 5px 0px 0px;
}
 .product-name{
    font-size: 17px;
    font-weight: 500;
}
 .product-detail{
    margin: 7px auto;
}
 .product-buynow{
    font-weight: 600;
}
#sample-flex{
    margin: auto;
    background-color: #fff;
    border-radius: 5px;
}